import { Component, OnInit } from '@angular/core';

import { Socket } from 'ng-socket-io';

declare var my_username : any;
declare var key:any;
declare var value:any;
declare var jQuery:any;
declare var $ :any;

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css']
})
export class ChatComponent implements OnInit {

  user:string;
  constructor(private socket:Socket) {
	var my_username : any;
   }
  ngOnInit() {
    var check = this;

    this.socket.on('customEve',function(data){
  
    $(function(){
      
    $('#datasend').click( function() {
      var message = $('#data').val();
      this.socket.emit('sendchat', message);
    });
    
    $('#data').keypress(function(e) {
      if(e.which == 13) {
        $(this).blur();
        //$('#datasend').focus().click();
        //$('#datasend').click();
        check.sendmsg();
      }
    });
  
    $('#users').click((event:any) => {
     
      check.send_individual_msg(event.srcElement.id);
      //$(this).socket.emit('check_user', my_username, event.srcElement.id);
      
       });
      
  });	
  
    });  
    
    this.socket.emit('adduser', prompt('What is your username?'));
   
    this.socket.on('msg_user_handle', function (username, data) {
      $('#conversation').append('<b>'+username + ':</b> ' + data + '<br>');			
    });
    
    
    this.socket.on('msg_user_found', function (username) {
      check.prompt(username);
      // this.socket.emit('msg_user', {username:username, my_username:my_username,msg: prompt("Type your message:")});
    });
    
    this.socket.on('updatechat', function (username, data) {
      $('#conversation').append('<b>'+username + ':</b> ' + data + '<br>');
    });
    
    
    this.socket.on('store_username', function (username) {
      this.my_username = username;
      check.getCurrentUser(this.my_username);
    });	
    this.socket.on('updateusers', function(data) {
      
      $('#users').empty();
      $.each(data,(key, valueF)=>{
        //$('#users').append('<div style="cursor:pointer;background-color:yellow" onclick="send_individual_msg(\''+value+'\')">' + key + '</div>');
        $('#users').append('<div id=\''+valueF+'\' style="cursor:pointer;background-color:yellow">' + key + '</div>');
      
      });
    });
  
      
  }
  getCurrentUser(val) {
    this.user=val;
  }
     send_individual_msg(id)
    {
     this.socket.emit('check_user', this.user, id);
      //this.socket.emit('msg_user', id, my_username, prompt("Type your message:"));
    }	
  
  sendmsg(){
    var msg= $('#data').val();
    if(msg!=""){
    $('#data').val('');
    this.socket.emit('sendchat', msg);
    }
  }
  prompt(username){
    this.socket.emit('msg_user', {username:username, my_username:this.user,msg: prompt("Type your message:")});
  }

}



